import Container from "../container/Container";

const Footer = () => {
    return (
        <footer id="footer">
            <Container>
                <section id="footer__content" className="row">
                    <h6>Portfólio Digital &copy; 2025 </h6>
                    <img width="100" src="./images/logo.png" alt="Logo"/>
                </section>
            </Container>
        </footer>
    )
}

export default Footer;